<script>
	import { getContext } from "svelte";
	import Button from "../library/Button.svelte";
	import Icon from "../library/Icon.svelte";
	import Separator from "../library/Separator.svelte";

	const _ = getContext("wx-i18n").getGroup("todo");

	let isValid;
	if (process.env.TRIALPACKAGE) {
		isValid = () => {
			if (typeof window === "undefined") return true;

			const l = location.hostname;
			const t = [
				// dhtmlx.com
				"ZGh0bWx4LmNvbQ==",
				// dhtmlxcode.com
				"ZGh0bWx4Y29kZS5jb20=",
				// webixcode.com
				"d2ViaXhjb2RlLmNvbQ==",
				// webix.io
				"d2ViaXguaW8=",
				// repl.co
				"cmVwbC5jbw==",
			];

			for (let i = 0; i < t.length; i++) {
				const x = window.atob(t[i]);
				if (x === l || l.endsWith("." + x)) return true;
			}

			return false;
		};
	}

</script>

<div class="wx-todo_button-add">
	<Button on:click height={36} width={'100%'}>
		<div class="wx-todo_button-add__content">
			<div class="wx-todo_button-add__icon">
				<Icon name={'plus'} size={16} />
			</div>
			<span class="wx-todo_button-add__text">{_('Add task')}</span>
		</div>
	</Button>
	<Separator />
	{#if process.env.TRIALPACKAGE && !isValid()}
		<mark
			class="wx-todo_mark"
			class:wx-todo_mark--error={process.env.TRIALDATE < new Date()}>
			{window.atob('VHJpYWw=')}
		</mark>
	{/if}
</div>

