<script>
	import Menu from "../Menu/Menu.svelte";
	import Icon from "../../library/Icon.svelte";

	import { changeLabelLocale } from "@xbs/lib-todo";
	import { getContext } from "svelte";

	export let api;

	$: state = api.getReactiveState();

	$: componentId = state.id;
	$: menu = state.menu;
	$: activeProject = state.activeProject;

	const locate = getContext("wx-i18n").getGroup("todo");
	$: options = changeLabelLocale($menu?.options || [], locate);

	let self = null;

	function itemClick(event) {
		const action = event.detail.action?.id ?? event.detail.action;

		api.exec("click-menu-item", {
			id: $activeProject,
			type: "toolbar",
			action,
			params: { locate },
		});
	}

	function toggleButtonClick() {
		const { menu, activeProject } = api.getState();

		if (menu?.open && menu.type === "toolbar") {
			api.exec("close-menu", {
				...menu,
			});
			return;
		}
		api.exec("open-menu", { id: activeProject, type: "toolbar" });
	}

</script>

<button
	bind:this={self}
	class="wx-todo_toolbar-menu-button"
	tabindex="0"
	data-toolbar-icon={true}
	data-todo-id={$componentId}>
	<Icon name="dots-v" on:click={toggleButtonClick} />
</button>

{#if $menu?.type === 'toolbar'}
	<Menu {options} parent={self} on:click={itemClick} />
{/if}

<style>
	.wx-todo_toolbar-menu-button {
		cursor: pointer;
		height: var(--wx-base-size);
		width: var(--wx-base-size);

		display: flex;
		align-items: center;
		justify-content: center;

		outline: none;
		border: none;
		border-radius: 50%;

		background: inherit;
	}
	.wx-todo_toolbar-menu-button:hover {
		background: var(--wx-icon-background-hover);
	}

</style>
