<script>
	import { Calendar } from "@xbs/svelte-wx";

	const prevent = event => event.preventDefault();

	export let item;

	$: store = item.store;
	$: value = item.value;

	const cancel = () => {
		const { menu } = store.getState();

		const selected = store.getSelection().map(id => ({
			...store.getTask(id),
			due_date: value || null,
		}));

		store.eachSelected((id, index) => {
			const task = store.getTask(id);
			store.in.exec("update-task", {
				id,
				task: {
					...task,
					due_date: value || null,
				},
				skipProvider: !!index,
				batch: !index ? selected.slice(1) : [],
			});
		});

		store.in.exec("close-menu", { ...menu });
	};

</script>

<div class="wx-todo_date-item" on:click={prevent}>
	<Calendar bind:value {cancel} />
</div>

<style>
	.wx-todo_date-item {
		min-width: 200px;
		max-width: 100%;
		width: 100%;
	}

</style>
