<script>
	import { afterUpdate } from "svelte";
	import { Menu, registerMenuItem } from "@xbs/svelte-menu";
	import { Portal } from "@xbs/svelte-wx";

	import Item from "./Items/Item.svelte";
	import UserItem from "./Items/UserItem.svelte";
	import DatePicker from "./Items/DatePicker.svelte";
	import PriorityItem from "./Items/PriorityItem.svelte";

	registerMenuItem("item", Item);
	registerMenuItem("user", UserItem);
	registerMenuItem("datepicker", DatePicker);
	registerMenuItem("priority", PriorityItem);

	export let options;
	export let parent;
	export let left = 0;
	export let top = 0;
	export let offsetTop = 5;
	export let offsetLeft = 5;

	let self = null;

	function positionToParent() {
		if (!parent) return;

		const menu = self.children[0];

		const parentRect = parent.getBoundingClientRect();
		const menuRect = menu.getBoundingClientRect();

		const scrollOffsetY = window.pageYOffset;
		const scrollOffsetX = window.pageXOffset;

		// always position left-bottom
		left = parentRect.right - menuRect.width - offsetLeft + scrollOffsetX;
		top = parentRect.bottom + offsetTop + scrollOffsetY;
	}

	afterUpdate(positionToParent);

</script>

<Portal theme="material">
	<div class="wx-menu" bind:this={self}>
		<Menu {options} {left} {top} on:click />
	</div>
</Portal>

<style>
	:global(.wx-portal .menu) {
		background: var(--wx-background);
		box-shadow: var(--wx-box-shadow);
		border-radius: var(--wx-border-radius);
	}
	:global(.wx-portal .menu .separator) {
		border-top: var(--wx-border);
	}
	:global(.wx-portal .menu .item) {
		height: max-content;
		width: max-content;
		min-width: 100%;
		padding: 0;
		margin: 0;
	}
	:global(.wx-portal .menu .item):hover {
		background: inherit;
	}
	:global(.wx-portal .menu .icon),
	:global(.wx-portal .menu .sub-icon) {
		display: none;
	}

</style>
