<script>
	import { onMount, getContext } from "svelte";

	let portal;
	export let theme = "";
	export let target = document.body;

	if (theme === "") theme = getContext("wx-theme");

	onMount(() => {
		target.appendChild(portal);
	});

</script>

<div class="wx-portal wx-{theme}-theme" bind:this={portal}>
	<slot />
</div>
